import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.expressPriority}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  // 查询数据
  query = () => {
    const {handleGetList, searchParams} = this.props
    handleGetList({searchParams: {...searchParams}})
  }

  // 重置
  reset = () => {
    const {update, handleGetList} = this.props
    update({searchParams: {}})
    handleGetList({searchParams: {}})
  }

  render() {
    const {
      searchParams: {provinceName}
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>省区：</label>
              <div className='box'>
                <Input value={provinceName} onChange={(e) => {
                  this.onInputChange({provinceName: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={18}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



