import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Form,
  Button,
  message,
  Table
} from 'antd'
import style from './style/Form.scss'
import {CsxSelect} from 'Form'
import {API_DELIVERYSETUP_GETADDR} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const confirm = Modal.confirm

@connect(
  state => ({...state.expressPriority, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    const countRight = {
      marginRight: 12
    }
    super()
    this.state = {
      loading_2: false,
      demandAccountFlag: 0
    }
    this.columns = [{
      title: '操作',
      width: 150,
      dataIndex: '',
      key: '',
      render: (text, record, index) => <div className="tableOperation">
        <span className="blue" onClick={() => {
          this.moveUp(text, record, index)
        }}>上移</span>
        <span className="blue" style={countRight} onClick={() => {
          this.moveDown(text, record, index)
        }}>下移</span>
        <span onClick={() => {
          this.onDel(text, record, index)
        }}>删除</span>
      </div>
    }, {
      title: '序号',
      width: 50,
      dataIndex: 'sortNumber',
      key: 'sortNumber',
      render: (text, record, index) => index + 1
    }, {
      title: '快递编号',
      width: 100,
      dataIndex: 'expressCode',
      key: 'expressCode'
    }, {
      title: '快递名称',
      width: 100,
      dataIndex: 'expressName',
      key: 'expressName'
    }]
  }
  onDel(text, record) {
    const {handleGetTableList, handleDel, modalData, userLoation} = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该条信息？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel({
          urlParams: [record.id]
        }, () => {
          message.success('删除成功')
          handleGetTableList({searchParams: {provinceCode: modalData.code, warehouseCode: userLoation.value}})
        })
      }
    })
  }

  // 上移
  moveUp(text, record, index) {
    console.log(record, 111)
    const {handleUpOrDown, handleGetTableList, modalData, userLoation} = this.props
    record.sortOperation = 'UP'
    handleUpOrDown({data: record, urlParams: [record.id]}, () => {
      let n = index + 1
      if (n === 1) {
        message.success('在顶部了，不能上移')
      } else {
        message.success('上移成功')
      }
      handleGetTableList({searchParams: {provinceCode: modalData.code, warehouseCode: userLoation.value}})
    })
  }

  //下移
  moveDown(text, record, index) {
    const {handleUpOrDown, handleGetTableList, dataResultLen, modalData, userLoation} = this.props
    record.sortOperation = 'DOWN'
    handleUpOrDown({data: record, urlParams: [record.id]}, () => {
      let n = index + 1
      if (n === dataResultLen) {
        message.success('在底部了，不能下移')
      } else {
        message.success('下移成功')
      }
      handleGetTableList({searchParams: {provinceCode: modalData.code, warehouseCode: userLoation.value}})
    })
  }

  componentDidMount() {
    //调用弹窗表格
    const {handleGetTableList, modalData, userLoation} = this.props
    handleGetTableList({searchParams: {provinceCode: modalData.code, warehouseCode: userLoation.value}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {update, tableData} = this.props
    update({tableData: {...tableData, ...values}})
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }

  // 增加快递商
  handleClick() {
    const {handleAddExpress, modalData, userLoation, handleGetTableList, tableData} = this.props
    handleAddExpress({
      data: {
        expressCode: tableData.express.value,
        expressName: tableData.express.children,
        provinceCode: modalData.code,
        provinceName: modalData.name,
        warehouseCode: userLoation.value,
        warehouseName: userLoation.children
      }
    }, () => {
      message.success('新增成功')
      handleGetTableList({searchParams: {provinceCode: modalData.code, warehouseCode: userLoation.value}})
    })
  }

  render() {
    const {
      popTableLoading,
      tableData: {
        express
      },
      popSearchResult,
      modalTitle
    } = this.props
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width={720}
      >
        <div className={style.form}>
          <Row>
            <Col span={14} offset={1}>
              <CsxSelect
                dateApi={API_DELIVERYSETUP_GETADDR}
                label='快递'
                selectItem={{data: express, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  console.log(value)
                  this.onInputChange({express: value})
                }}
              />
            </Col>
            <Col span={5} offset={4}>
              <Button type="primary" onClick={() => {
                this.handleClick()
              }}>新增</Button>
            </Col>
          </Row>
        </div>
        <div className='table-group'>
          <Table
            rowKey={record => record.id}
            loading={popTableLoading}
            columns={this.columns}
            dataSource={popSearchResult}
            onChange={this.onChange}
            scroll={{x: 410, y: 300}}
            pagination={false}
          />
        </div>
      </Modal>
    )
  }
}
export default Form.create()(App)



