import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.expressPriority, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.columns = [{
      title: '操作',
      width: 100,
      dataIndex: '',
      key: '',
      render: (text, record, index) => <div className="tableOperation">
        <span className="blue" onClick={() => {
          this.onEdit(text, record, index)
        }}>快速配置</span>
      </div>
    }, {
      title: '省区',
      width: 150,
      dataIndex: 'name',
      key: 'name'
    }]
  }

  onEdit(text, record, index) {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '快递优先级', modalData: record})
  }
  componentDidMount() {
    const {handleGetList} = this.props
    handleGetList({searchParams: {}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {...newProps.searchParams}})
    }
  }

  render() {
    const {
      tableLoading,
      searchResult
    } = this.props
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={false}
        />
      </div>
    )
  }
}



