import * as actionType from './actionType'
import {getData} from 'util'
import {API_EXPRESSPRIORITYCHOICESPROVINCES_PAGE, API_EXPRESSPRIORITY_PAGE, API_EXPRESSPRIORITYCHOICES_CREATE, API_EXPRESSPRIORITYCHOICES_DELETE, API_EXPRESSPRIORITYCHOICES_SORT} from 'api'
//同步
export const update = (object) => ({
  type: actionType.EXPRESSPRIORITY_UPDATE,
  data: object
})

//异步

// 获取省区列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.EXPRESSPRIORITY_UPDATE, data: {tableLoading: true}})
  getData({api: API_EXPRESSPRIORITYCHOICESPROVINCES_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.EXPRESSPRIORITY_UPDATE,
      data: {
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.EXPRESSPRIORITY_FILE
    })
  })
}

//获取弹窗列表
export const handleGetTableList = (reqData) => (dispatch) => {
  dispatch({type: actionType.EXPRESSPRIORITY_UPDATE, data: {popTableLoading: true}})
  getData({api: API_EXPRESSPRIORITY_PAGE, ...reqData}).then(data => {
    console.log(data.result)
    let array = []
    data.result.map(o => {
      array.push(o.sortNumber)
    })
    console.log(array)
    // 相应的逻辑判断
    dispatch({
      type: actionType.EXPRESSPRIORITY_UPDATE,
      data: {
        popTableLoading: false,
        dataResultLen: data.result.length,
        popSearchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.EXPRESSPRIORITY_FILE
    })
  })
}

//新增快递商
export const handleAddExpress = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.EXPRESSPRIORITY_UPDATE, data: {popTableLoading: true}})
  let api = API_EXPRESSPRIORITYCHOICES_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.EXPRESSPRIORITY_UPDATE,
      data: {
        popTableLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.EXPRESSPRIORITY_FILE
    })
  })
}

// 删除快递商
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_EXPRESSPRIORITYCHOICES_DELETE, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.EXPRESSPRIORITY_FILE
    })
  })
}

// // 上移或下移快递商
export const handleUpOrDown = (reqData, callBack) => (dispatch) => {
  getData({api: API_EXPRESSPRIORITYCHOICES_SORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.EXPRESSPRIORITY_FILE
    })
  })
}

